}
.detailContainer {
  background-color: rgba(255, 255, 255, 0.7);
  display: flex;
  flex-direction: column;
  justify-content: right;
  align-items: flex-end;
  animation: fadeIn 1s;
  -webkit-animation: fadeIn 1s;
  -moz-animation: fadeIn 1s;
  -o-animation: fadeIn 1s;
  -ms-animation: fadeIn 1s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.detailButton {
  margin: 0;
  padding: 0;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  color: #000;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1rem;
  cursor: pointer;
}

.detailButton:hover {
  font-weight: bold;
}

.detailInfo {
  margin-left: auto;
  margin-right: auto;
  padding: 0.5em;
}

.detailsTitle {
  font-size: 2rem;
}
.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0, 0, 0, 0.95);
    color: #fff;
}

.popup_inner {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* left: 20%;
    right: 20%;
    top: 2vh; */
    height: 100vh;
    
    border-radius: 15px;
    
    margin: 0;
    padding: 10px;
}

@media screen and (max-width: 720px), (orientation: portrait) {
    .popup_inner{
        max-height: 100vh;
    }
    .imgExpand img{
        max-width: 90%;
        margin-left: 5%;
        margin-left: 5%;
    }
    .buttonExpand{
